import React, { Component } from 'react';
import { withTranslation } from 'react-i18next';
import { withRouteData } from 'react-static'

import Description from "./description/Description";
import Client from "./client/Client";
import PortfolioCompleted from "./portfolioCompleted/PortfolioCompleted";
import Portfolio from "./portfolio/Portfolio";
import RedesUnity from "./redes/Redes";
import Team from "../about-us/AboutUs";

import './Unit.css'
import './styles/Unit-Soft.css'
import './styles/Unit-IoT.css'
import './styles/Unit-Games.css'
import './styles/Unit-Science.css'
import './styles/Unit-School.css'
import './styles/Unit-Media.css'


class Unit extends Component {

  render() {

    const { t, data } = this.props;

    return (
      <div className={`Unit ${data.unit}`} style={{
        background:  `radial-gradient(ellipse at center, rgba(255,255,255,1) 10%,rgba(200,200,200,1) 100%)`
      }}>
        <Description unity={data} />
        {
          (data.clients && data.clients.active) ? (
            <div> {/* new component */}
              <p style={{ color: data.style.title }} className="title-services">{t('unit.clients')}</p>
              <div className="container-clients">
                {
                  data.clients.list.map(
                    (client, index) => <Client key={index} client={client} color={data.style.general} />
                  )
                }
              </div>
            </div>
          ) : ""
        }
        {
          (data.portfolio && data.portfolio.active) ? (
            <div> {/* new component */}
              <p style={{ color: data.style.title }} className="title-services">{t('unit.portfolio')}</p>
              <div className="container-portfolioCompleted">
                {
                  data.portfolio.completed.map(
                    (portfolioCompleted, index) =>
                      <PortfolioCompleted
                        key={index}
                        index={index}
                        portfolioCompleted={portfolioCompleted}
                        style={data.style}
                        marcoServices={data.marcoServices}
                      />
                  )
                }
              </div>
              <div className="container-portfolioOngoing">
                {
                  data.portfolio.ongoing.map(
                    (portfolioOngoing, index) =>
                      <Portfolio
                        key={index}
                        index={index}
                        portfolioOngoing={portfolioOngoing}
                        marcoLargelOngoing={data.marcoLargelOngoing}
                        style={data.style}
                      />
                  )
                }
              </div>
            </div>
          ) : ""
        }
        <Team/>
        <RedesUnity redes={data.redes} />
      </div>
    );
  }
}

export default withRouteData(withTranslation()(Unit));
